import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Stripe } from '@ionic-native/stripe';

@Component({
  selector: 'page-stripe-native',
  templateUrl: 'stripe-native.html',
})
export class StripeNativePage {

  cardNumber: string;
  cardMonth: number;
  cardYear: number;
  cardCVV: string;

  constructor(public navCtrl: NavController, public navParams: NavParams, public stripe: Stripe) {

  }

  ionViewDidLoad() {
    this.stripe.setPublishableKey('pk_test_ad251ef6dcf08a4ee27af892d1210f4a3108984e7e20f57220fba03d7adbff78');
  }

  splitExpiry(){

  }

  validateCard(){
    let card = {
      number: this.cardNumber,
      expMonth: this.cardMonth,
      expYear: this.cardYear,
      cvc: this.cardCVV
     };

     // Run card validation here and then attempt to tokenise

     this.stripe.createCardToken(card)
      .then(token => {
          alert(token.id);
      })
        .catch(error => console.error(error));
  }

}



